.sale {
  position: absolute;
  width: 48px;
  height: 48px;
  padding: 15px 8px;
  font-weight: 500;
  font-size: 12px;
  line-height: 150%;
  color: $white;
  border-radius: 100%;
  background: linear-gradient(135deg, #7a0000 0%, #900 100%);
